<template>
  <div class="update-name">
    <!--  -->
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />
    <!-- / -->
    <!--  -->
    <div class="field-wrap">
      <van-field
        v-model.trim="localName"
        rows="2"
        autosize
        type="textarea"
        maxlength="7"
        placeholder=""
        show-word-limit
      />
    </div>
    <!-- / -->
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'

export default {
  name: 'UpdateName',
  props: {
    value: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      localName: this.value
    }
  },
  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 // 
      })

      try {
        const localName = this.localName
        if (!localName.length) {
          this.$toast('')
          return
        }

        await updateUserProfile({
          name: localName
        })

        // 
        this.$emit('input', localName)

        // 
        this.$emit('close')

        // 
        this.$toast.success('')
      } catch (err) {
        if (err && err.response && err.response.status === 409) {
          this.$toast.fail('')
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
.field-wrap {
  padding: 20px;
}
</style>
